 <h4><span class="heading5">Introduction</span></h4>
<p>Glide is a task-based daily scheduler for employees with Autism Spectrum Disorder and their managers. It was designed for a fourth-year capstone project by, me, <a class="link" target="_blank" href="https://www.linkedin.com/in/brennemandaniel/">Daniel Brenneman</a>, <a class="link" target="_blank" href="https://www.linkedin.com/in/sarah-stock-528488106/">Sarah Stock</a>, <a class="link" target="_blank" href="https://www.linkedin.com/in/sonjakatanic/">Sonja Katanic</a>, and <a class="link" target="_blank" href="https://www.linkedin.com/in/devon-bradwell/">Devon Bradwell.</a></p>
<p>This project went from an idea to a fully designed prototype. The team is now looking for funding to support the development of this application.</p>
  </b-col>
