   <h3>Adam</h3>
<p>Adam is a student who has just started Highschool - he was diagnosed with ASD a few years ago.</p>
  <h5>Activities</h5>
  <ul>
  <li>Plays Volleyball</li>
  <li>Goes to the robotics lounge at lunch</li>
  <li>Spends most weekends playing video games</li>
  <li>Goes to piano lessons by himself on Saturdays</li>
  </ul>
  <h5>Behaviours</h5>
  <ul>
    <li>Shutdowns occur within 10 minutes</li>
    <li>Going off schedule can lead to shutdowns that last over an hour</li>
    <li>Likes to keep his schedule open but has a task manager</li>
    <li>On the milder side of the spectrum</li>
  </ul>
  </b-col>
     <b-col cols="12" sm="12" md="4" lg="4" xl="4">
   <h3>Adam's Schedule</h3>
  <ul>
  <li>Wake's up at 7am</li>
  <li>Gets ready 7 - 8am</li>
  <li>Catch's bus 8:15am</li>
  <li>Arrives at school 8:50am</li>
  <li>School Schedule 9:00am - 11:50 am</li>
  <li>Lunch at the Robotics Lounge with Patrick 12 - 1pm</li>
  <li>School Schedule 1 - 3pm</li>
  <li>Catchs bus home 3:15pm</li>
  <li>Arrives Home 3:50pm</li>
  <li>Plays Video Games until 6pm</li>
  <li>Parents come home and have dinner 6 - 7:30pm</li>
  <li>Homework 7:30pm - 9pm</li>
  <li>Gets ready for bed 9 - 10pm</li>
  <li>Sleep 10pm</li>
  </ul>
